/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: $grey-100;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 12px;
}

*::-webkit-scrollbar-track {
  background: $blue-100;
}

*::-webkit-scrollbar-thumb {
  background-color: $blue-200;
  border-radius: 5px;
  border: 3px solid $blue-100;
}

#app {
  position: relative;
  overflow: hidden;
  height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr
}

.flex {
  display: flex;

  &.between {
    justify-content: space-between;
  }

  &.items-center {
    align-items: center;
  }
}

#chatContainer {
  width: 100%;
  overflow: hidden;
  margin-bottom: 2rem;
}

.main-row {
  height: 100%;
}

.messages--wrapper, .botStats {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}

.botStats {
  background-color: #fff;
  border-radius: 3px;
}

.messages--wrapper {
  position: relative;

  header {
    height: 50px;
    display: flex;
    justify-content: flex-end;
    background-color: #fff;
    #search_form {
      display: flex;
      align-items: center;
    }
  }

  footer {
    position: absolute;
    bottom: 0;
    width: 100%;
  }
}

