.main-header {
  background-color: $blue-300;
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
  .h1, ,h2 {
    color: #fff;
    margin: 0;
    padding: 0;
  }
  .h1 {
    font-size: 1.5rem;
  }
  .h2 {
    font-size: 0.9rem;
    opacity: 0.6;
  }
}

.container {
  width: 100%;
  max-width: 960px;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
}

.row {
  display: grid;
  grid-template-columns: 5fr 2fr;
  grid-gap: 1.5rem
}

.tickets {
  height: calc(100% - 130px);
  overflow: auto;
}
